@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  background-color: #0a3d62;
  font-family: 'Lexend Deca', sans-serif; }

button,
input {
  font-family: 'Lexend Deca', sans-serif; }

body,
#root {
  height: 100vh; }

.rotate {
  -webkit-animation: rotation 1s infinite linear;
          animation: rotation 1s infinite linear; }

.game-overlay::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  background-color: rgba(255, 255, 255, 0.75); }
  @media screen and (min-width: 768px) {
    .game-overlay::before {
      display: none; } }

.game-overlay h2 {
  font-size: 3rem; }
  @media screen and (min-width: 768px) {
    .game-overlay h2 {
      font-size: 5.4rem; } }

.game-overlay img {
  display: none; }
  @media screen and (min-width: 768px) {
    .game-overlay img {
      display: block; } }

.remaining {
  margin: 10px 0;
  font-size: 1.6em; }
  @media screen and (min-width: 768px) {
    .remaining {
      margin: 20px 0;
      font-size: 2rem; } }

.col {
  -webkit-user-select: none;
  -webkit-touch-callout: none; }
  .col span[aria-label="flag"] {
    display: block;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: rotate(10deg) scale(10);
            transform: rotate(10deg) scale(10);
    -webkit-transition: .2s transform ease-in-out;
    transition: .2s transform ease-in-out; }
  .col .animated span[aria-label="flag"] {
    opacity: 1;
    visibility: visible;
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1); }
  .col .suggested {
    position: relative; }
    .col .suggested::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      display: block;
      width: 26px;
      height: 26px;
      background-color: rgba(241, 196, 15, 0.5);
      border-radius: 50%;
      -webkit-animation: pulse 1s infinite ease-in-out;
              animation: pulse 1s infinite ease-in-out; }

@-webkit-keyframes rotation {
  from {
    -webkit-transform: translateX(-50%) rotate(0deg);
            transform: translateX(-50%) rotate(0deg); }
  to {
    -webkit-transform: translateX(-50%) rotate(359deg);
            transform: translateX(-50%) rotate(359deg); } }

@keyframes rotation {
  from {
    -webkit-transform: translateX(-50%) rotate(0deg);
            transform: translateX(-50%) rotate(0deg); }
  to {
    -webkit-transform: translateX(-50%) rotate(359deg);
            transform: translateX(-50%) rotate(359deg); } }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1); }
  50% {
    -webkit-transform: translate(-50%, -50%) scale(1.2);
            transform: translate(-50%, -50%) scale(1.2); }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1); } }

@keyframes pulse {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1); }
  50% {
    -webkit-transform: translate(-50%, -50%) scale(1.2);
            transform: translate(-50%, -50%) scale(1.2); }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1); } }

